*{
  font-family: Helvetica, Arial, sans-serif;
}

span{
  font-weight: 900;
  font-size: 30px;
}

body{
  padding: 30px !important;
  background-color: aliceblue;
}

input{
  color: gray;
  font-size: 1.3rem;
  width: 100%;
  border-radius: 15px;
}

.card-grid{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  background-color: #29292c;
  border-radius: 6px;
  margin: 5px;
  overflow: hidden;
}

.card p{
  color:aliceblue;
  font-weight: bold;
  text-align: center;
  max-width: 200px;
}

.card img{
  width: 200px;
  height: 200px;
  padding: 5px;
  border-radius: 8px;
}

ul{
  padding:0px;
}